.tags-wrap {
    width: 100px;
    position: fixed;
    left: 100px;
    bottom: 100px;
    top: 130px;
    background: #f5f5f5;
    padding: 10px;
    border-radius: 20px;
}

.tag-name {
    font-family: Pretendard, sans-serif;
    font-size: 17px;
    padding: 5px;
    background: #f5f5f5;
    transition: 0.15s;
    border-radius: 10px;
    margin: 10px auto;
    text-align: center;
    color: black;
}

.tag-selected {
    color: #02cb3f !important;
    background: #e6e6e6 !important;
}

.t-name {
    text-decoration: none;
}

.tag-name:hover {
    color: #02cb3f;
    background: #e6e6e6;
    transition: 0.15s;
}

@media (max-width: 1600px) {
    .tags-wrap {
        left: 30px;
    }
}

@media (max-width: 1440px) {
    #tag-title {
        margin-bottom: 80px;
    }

    .tags-wrap {
        width: 100%;
        height: 20px;
        position: relative;
        margin-top: -190px;
        margin-bottom: 150px;
        left: -5px;
    }

    .tag-name {
        margin-top: -5px;
        max-width: 80px;
        float: left;
        margin-right: 10px;
    }
}

@media (max-width: 723px) {
    .tags-wrap {
        height: 55px;
    }
}