.chart-body {
    transition: 0.1s;
    padding: 10px 0;
    margin-top: -9px;
    margin-bottom: 12px;
    color: black;
}

.c-body {
    text-decoration: none;
}

.chart-body:hover {
    transition: 0.1s;
    background: #f5f5f5;
}

.chart-pos {
    font-family: Pretendard, sans-serif;
    font-size: 25px;
    text-align: center;
    width: 50px;
    padding-top: 35px;
    padding-bottom: 35px;
    font-weight: 1000;
}

.chart-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    position: absolute;
    margin-top: -100px;
    margin-left: 50px;
}

.chart-status {
    position: absolute;
    line-height: 100px;
    margin: -100px 10px 0 160px;
    font-family: Pretendard, sans-serif;
    font-size: 20px;
    color: #ff0000;
}

.chart-status > i.fa-chevron-up {
    margin-left: 10px;
    margin-top: 30px;
    position: absolute;
    color: #00cc00;
}

.increase-value {
    width: 50px;
    text-align: center;
    margin-left: -7px;
    margin-top: 10px;
    position: absolute;
    color: #00cc00;
}

.chart-status > i.fa-chevron-down {
    margin-left: 10px;
    margin-top: 55px;
    position: absolute;
    color: #0000ff;
}

.decrease-value {
    width: 50px;
    text-align: center;
    margin-left: -7px;
    margin-top: -5px;
    position: absolute;
    color: #0000ff;
}

.chart-status > i.fa-minus {
    margin-top: 40px;
    position: absolute;
    margin-left: 12px;
    color: #808080;
}

.chart-title {
    font-size: 25px;
    font-family: Pretendard, sans-serif;
    font-weight: 1000;
    margin-left: 215px;
    margin-top: -75px;
    position: absolute;
    z-index: 0;
    max-width: 540px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.chart-artist {
    font-size: 19px;
    font-family: Pretendard, sans-serif;
    position: absolute;
    margin-left: 215px;
    margin-top: -45px;
    color: #666666;
    max-width: 540px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.right-align {
    float: right;
    margin-top: -70px;
    font-size: 18px;
    font-family: Pretendard, sans-serif;
    margin-right: 30px;
}

.chart-last {
    position: absolute;
    margin-left: -270px;
}

.chart-peak {
    position: absolute;
    margin-left: -175px;
}

.chart-date {
    position: absolute;
    margin-left: -90px;
}

.chart-views {
    margin-right: 30px;
    float: right;
    margin-top: -45px;
    font-size: 18px;
    font-family: Pretendard, sans-serif;
    color: #404040;
}

.chart-header {
    width: 100%;
    margin: 0 auto;
    padding: 5px 15px 20px 15px;
    font-family: Pretendard, sans-serif;
    font-size: 18px;
    font-weight: bold;
}

#header-pos {
    margin-left: -5px;
}

#header-title {
    margin-left: 170px;
}

#header-artist {
    position: absolute;
    margin-top: 22px;
    font-size: 15px;
    margin-left: -29px;
    color: #404040;
}

.header-right-align, .header-right-align-chart {
    float: right;
    margin-top: -20px;
    margin-right: 40px;
}

.header-right-align-chart {
    margin-top: 0;
}

#header-last {
    position: absolute;
    margin-left: -285px;
}

#header-peak {
    position: absolute;
    margin-left: -185px;
}

#header-date {
    position: absolute;
    margin-left: -75px;
}

#header-views {
    position: absolute;
    margin-top: 22px;
    font-size: 15px;
    margin-left: -78px;
    color: #404040;
}

.chart-menu {
    width: 100%;
    background: #f6f6f6;
    margin-top: -8px;
    margin-bottom: -20px;
    padding: 10px 0;
}

.chart-menu-text {
    color: black;
    text-decoration: none;
    font-family: Pretendard, sans-serif;
    font-size: 20px;
    margin-right: 20px;
    margin-left: 15px;
    transition: 0.1s;
}

.selected {
    color: #02cb3f;
}

.chart-menu-text:hover {
    color: #02cb3f;
    transition: 0.1s;
}

.chart-menu-separate {
    margin-top: 20px;
    height: 1px;
    background: #9f9f9f;
    border: none;
}

.chart-type {
    font-family: Pretendard, sans-serif;
    font-size: 30px;
    margin-top: 50px;
    margin-bottom: 50px;
}

.chart-separate-line {
    height: 1px;
    background: #9f9f9f;
    border: none;
    margin-bottom: 9px;
}

@media (max-width: 1000px) {
    .chart-title {
        font-size: 22px;
        max-width: 300px;
    }

    .chart-artist {
        font-size: 17px;
        max-width: 300px;
    }
}

@media (max-width: 900px) {
    .chart-title, .chart-artist {
        max-width: 200px;
    }
}

@media (max-width: 800px) {
    #header-last, .chart-last, #header-peak, .chart-peak {
        display: none;
    }

}

@media (max-width: 600px) {
    #header-views, #header-date, .chart-views, .chart-date {
        display: none;
    }

    .chart-title, .chart-artist {
        max-width: 230px;
    }
}

@media (max-width: 500px) {
    .chart-body {
        height: 75px;
    }

    #header-pos {
        margin-left: 0;
    }

    .chart-pos {
        padding-top: 20px;
        padding-bottom: 25px;
    }

    .chart-image {
        margin-left: 45px;
        width: 75px;
        height: 75px;
        margin-top: -75px;
    }

    .chart-status {
        display: none;
    }

    #header-title {
        margin-left: 130px;
    }

    .chart-title {
        margin-left: 130px;
        font-size: 17px;
        margin-top: -60px;
    }

    .chart-artist {
        margin-left: 130px;
        font-size: 15px;
        margin-top: -35px;
    }
}