@font-face {
    font-family: 'Pretendard';
    src: url('../fonts/Pretendard.otf') format('truetype');
}

@font-face {
    font-family: 'LeferiBaseType';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/LeferiBaseType-BoldA.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'GmarketSansBold';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansBold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: white;
}

.container {
    width: 70%;
    margin: 0 auto;
}

#header-logo {
    width: 150px;
    padding: 20px 0;
    margin-top: 30px;
}

.search-btn-wrap {
    position: absolute;
    top: 45px;
    margin-left: 190px;
}

.search-input {
    font-size: 17px;
    width: 400px;
    height: 30px;
    padding: 8px 10px 8px 35px;
    border: 2px solid #02cb3f;
    font-family: Pretendard, sans-serif;
    outline: 0;
}

.fa-magnifying-glass {
    font-size: 20px;
    border: none;
    background: none;
    position: absolute;
    margin: 14px 9px;
    color: #02cb3f;
}

#search-result {
    position: absolute;
    margin-top: 10px;
    z-index: 99;
}

.result-item {
    margin: -1px auto 0;
    border: 1px solid black;
    padding: 5px 10px;
    width: 427px;
    height: 50px;
    font-family: Pretendard, sans-serif;
    color: black;
    transition: 0.2s;
    background: white;
}

.result-item:hover {
    background: #f5f5f5;
}

.search {
    text-align: center;
}

.search-image {
    width: 50px;
    height: 50px;
    object-fit: cover;
    position: absolute;
}

.result-vertical {
    border-left: 2px solid black;
    height: 40px;
    margin-top: 5px;
    position: absolute;
    margin-left: 60px;
}

.result-title {
    margin-left: 70px;
    margin-top: 5px;
    position: absolute;
    max-width: 270px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.result-artist {
    position: absolute;
    margin-left: 70px;
    margin-top: 25px;
    max-width: 270px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.result-date {
    position: absolute;
    right: 10px;
    margin-top: 17px;
    font-size: 15px;
    color: #4d4d4d;
}

.menu-vertical {
    border-left: 2px solid black;
    height: 40px;
    position: absolute;
    top: 50px;
    margin-left: 670px;
}

.menu {
    position: absolute;
    top: 57px;
    margin-left: 700px;
}

.menu-item {
    margin-right: 30px;
    font-family: Pretendard, sans-serif;
    font-size: 20px;
    color: black;
    text-decoration: none;
    transition: 0.1s;
}

.menu-item:hover {
    color: #02cb3f;
    transition: 0.1s;
}

.title {
    margin-top: 60px;
    font-family: LeferiBaseType, sans-serif;
    font-size: 40px;
}

.title-image {
    margin-top: -100px;
    width: 400px;
    float: right;
}

.title-image-desc {
    float: right;
    font-family: Pretendard, sans-serif;
    font-size: 15px;
    margin-top: 480px;
    margin-right: -350px;
}

.title-desc {
    font-size: 19px;
    font-family: Pretendard, sans-serif;
    margin-bottom: 300px;
}

.section-title {
    margin-top: 100px;
    font-family: Pretendard, sans-serif;
    font-size: 35px;
    font-weight: 1000;
}

.more {
    color: #4d4d4d;
    font-family: Pretendard, sans-serif;
    float: right;
    margin-top: -50px;
    text-decoration: none;
    transition: 0.1s;
}

.more:hover {
    text-decoration: underline;
    transition: 0.1s;
}

.section-separate {
    height: 1px;
    background: #9f9f9f;
    border: none;
    margin-top: -15px;
}

.footer {
    width: 100%;
    height: 120px;
    text-align: center;
    margin: 50px auto;
    position: static;
}

#footer-logo {
    margin: 30px auto 0;
    width: 200px;
    transform: translateZ(0);
    backface-visibility: hidden;
    image-rendering: crisp-edges;
}

#footer-text {
    margin-top: 20px;
    font-family: Pretendard, sans-serif;
    font-size: 18px;
}

#footer-inform {
    color: gray;
    font-family: Pretendard, sans-serif;
    font-size: 18px;
    text-align: center;
    margin-top: 10px;
    margin-right: 20px;
}

.credit-title {
    font-family: Pretendard, sans-serif;
    font-weight: 1000;
    font-size: 30px;
}

.credit-content {
    font-family: Pretendard, sans-serif;
    font-size: 20px;
    line-height: 30px;
}

.fade-in-page {
    animation: fadein 0.5s;
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeout {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}


.preloader {
    margin: 0 auto;
    width: 100px;
    height: 100px;
    transform: translateX(-50%) translateY(-50%);
    animation: rotatePreloader 2s infinite ease-in;
}

@keyframes rotatePreloader {
    0% {
        transform: translateX(-50%) translateY(-50%) rotateZ(0deg);
    }
    100% {
        transform: translateX(-50%) translateY(-50%) rotateZ(-360deg);
    }
}

.preloader div {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
}

.preloader div:before {
    content: "";
    position: absolute;
    left: 50%;
    top: 0;
    width: 10%;
    height: 10%;
    background-color: #1a48c4;
    transform: translateX(-50%);
    border-radius: 50%;
}

.preloader div:nth-child(1) {
    transform: rotateZ(0deg);
    animation: rotateCircle1 2s infinite linear;
    z-index: 9;
}

@keyframes rotateCircle1 {
    0% {
        opacity: 0;
    }
    0% {
        opacity: 1;
        transform: rotateZ(36deg);
    }
    7% {
        transform: rotateZ(0deg);
    }
    57% {
        transform: rotateZ(0deg);
    }
    100% {
        transform: rotateZ(-324deg);
        opacity: 1;
    }
}

.preloader div:nth-child(2) {
    transform: rotateZ(36deg);
    animation: rotateCircle2 2s infinite linear;
    z-index: 8;
}

@keyframes rotateCircle2 {
    5% {
        opacity: 0;
    }
    5.0001% {
        opacity: 1;
        transform: rotateZ(0deg);
    }
    12% {
        transform: rotateZ(-36deg);
    }
    62% {
        transform: rotateZ(-36deg);
    }
    100% {
        transform: rotateZ(-324deg);
        opacity: 1;
    }
}

.preloader div:nth-child(3) {
    transform: rotateZ(72deg);
    animation: rotateCircle3 2s infinite linear;
    z-index: 7;
}

@keyframes rotateCircle3 {
    10% {
        opacity: 0;
    }
    10.0002% {
        opacity: 1;
        transform: rotateZ(-36deg);
    }
    17% {
        transform: rotateZ(-72deg);
    }
    67% {
        transform: rotateZ(-72deg);
    }
    100% {
        transform: rotateZ(-324deg);
        opacity: 1;
    }
}

.preloader div:nth-child(4) {
    transform: rotateZ(108deg);
    animation: rotateCircle4 2s infinite linear;
    z-index: 6;
}

@keyframes rotateCircle4 {
    15% {
        opacity: 0;
    }
    15.0003% {
        opacity: 1;
        transform: rotateZ(-72deg);
    }
    22% {
        transform: rotateZ(-108deg);
    }
    72% {
        transform: rotateZ(-108deg);
    }
    100% {
        transform: rotateZ(-324deg);
        opacity: 1;
    }
}

.preloader div:nth-child(5) {
    transform: rotateZ(144deg);
    animation: rotateCircle5 2s infinite linear;
    z-index: 5;
}

@keyframes rotateCircle5 {
    20% {
        opacity: 0;
    }
    20.0004% {
        opacity: 1;
        transform: rotateZ(-108deg);
    }
    27% {
        transform: rotateZ(-144deg);
    }
    77% {
        transform: rotateZ(-144deg);
    }
    100% {
        transform: rotateZ(-324deg);
        opacity: 1;
    }
}

.preloader div:nth-child(6) {
    transform: rotateZ(180deg);
    animation: rotateCircle6 2s infinite linear;
    z-index: 4;
}

@keyframes rotateCircle6 {
    25% {
        opacity: 0;
    }
    25.0005% {
        opacity: 1;
        transform: rotateZ(-144deg);
    }
    32% {
        transform: rotateZ(-180deg);
    }
    82% {
        transform: rotateZ(-180deg);
    }
    100% {
        transform: rotateZ(-324deg);
        opacity: 1;
    }
}

.preloader div:nth-child(7) {
    transform: rotateZ(216deg);
    animation: rotateCircle7 2s infinite linear;
    z-index: 3;
}

@keyframes rotateCircle7 {
    30% {
        opacity: 0;
    }
    30.0006% {
        opacity: 1;
        transform: rotateZ(-180deg);
    }
    37% {
        transform: rotateZ(-216deg);
    }
    87% {
