#loading-text {
    width: 100%;
    margin-left: -40px;
    margin-bottom: 30px;
    font-size: 25px;
    font-family: Pretendard, sans-serif;
    color: black;
    text-align: center;
}


.mobile-menu {
    display: none;
    color: black;
    padding: 10px;
    font-size: 30px;
}

.mobile-logo {
    width: 45%;
    padding: 30px 10px 0;
}

.mob-menu {
    position:absolute;
    top:0;
    right:0;
    width:50px;
    height:50px;
    cursor:pointer;
}

.burger {
    right: 15px;
    top: 23px;
    position: absolute;
    width: 20px;
    height: 2px;
    background: #000000;
    z-index: 10;
}

.burger .top-line {
    width: 20px;
    height: 2px;
    background-color: #000000;
    bottom: -5px;
    content: '';
    position: absolute;
    right: 0;
    -webkit-transition: -webkit-transform .3s;
    transition: transform .3s;

}

.burger .bot-line {
    width: 20px;
    height: 2px;
    background-color: #000000;
    top: -5px;
    content: '';
    position: absolute;
    right: 0;
    -webkit-transition: -webkit-transform .3s;
    transition: transform .3s;
}

.burger.on {
    width: 0;
}

.burger.on .bot-line {
    transform: translateY(5px) rotate(135deg);
}

.burger.on .top-line {
    transform: translateY(-5px) rotate(-135deg);
}

.menu-slide {
    width: 50%;
    position: absolute;
    left: -100%;
    height: 100vh;
    top: 0;
    right: 0;
    bottom: 0;
    transition: left .3s;
    background-color: #f5f5f5;
    z-index: 9;
    box-shadow: 0 0 5px 0;
}

.menu-slide.on {
    left: 0;
}

.menu-slide ul li {
    display: inline-block;
    width: 100%;
    padding: 8px 30px 8px 0;
    border-bottom: 1px solid #4d4d4d;
    font-size: 20px;
    font-family: Pretendard, sans-serif;
    color: black;
    margin-left: -30px;
    transition: 0.2s;
}

.menu-slide ul li:hover {
    color: #02cb3f;
    transition: 0.2s;
}

@media (max-width: 1440px) {
    .container {
        width: 90%;
    }
}

@media (max-width: 1300px) {
    .container {
        width: 90%;
    }
}

@media (max-width: 1220px) {
    .menu-vertical {
        display: none;
    }

    .search-btn-wrap {
        display: none;
    }

    .menu {
        margin-left: 180px;
    }

    .title-image {
        width: 350px;
    }

    .title-desc {
        margin-bottom: 200px;
    }
}

@media (max-width: 1140px) {
    .container {
        width: 93%;
    }

    .title-image-desc {
        margin-left: -30px;
    }
}

@media (max-width: 850px) {
    .container {
        width: 88%;
    }

    .title-image {
        width: 300px;
    }

    .title-image-wrap {
        margin-top: 100px;
        float: none;
        text-align: center;
    }

    .title-image-desc {
        float: none;
        position: absolute;
        margin-right: 0;
        margin-top: 330px;
    }

    .title-image {
        float: none;
    }

    .title-desc {
        font-size: 15px;
    }

    .title {
        font-size: 30px;
    }
}

@media (max-width: 700px) {
    .menu {
        display: none;
    }

    .mobile-menu {
        display: block;
    }

    #footer-text {
        font-size: 14px;
    }

    #footer-inform {
        font-size: 12px;
    }

    #footer-logo {
        width: 150px;
    }

    .footer {
        height: 150px;
    }

    .container {
        width: 87%;
    }
}
