.new-wrap {
    display: flex;
    margin-bottom: 40px;
    flex-flow: row wrap;
    width: 100%;
}

.new-body {
    width: 150px;
    transition: 0.2s;
    margin-right: 98px;
    margin-left: -10px;
    padding: 12px;
    border-radius: 15px;
    color: black;
    text-decoration: none;
    margin-bottom: 30px;
}

.new-body:hover {
    background: #f5f5f5;
    transition: 0.2s;
}

.new-image {
    width: 150px;
    height: 150px;
    object-fit: cover;
}

.new-title {
    margin-top: 6px;
    width: 250px;
    font-family: Pretendard, sans-serif;
    font-size: 20px;
    max-width: 140px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.new-artist, .new-date {
    font-family: Pretendard, sans-serif;
    font-size: 15px;
    color: #4d4d4d;
    max-width: 140px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.new-section-title {
    margin-top: 0;
    font-family: Pretendard, sans-serif;
    font-size: 30px;
    margin-bottom: 20px;
}

.invisible {
    visibility: hidden;
}

@media (max-width: 1600px) {
    .new-body {
        margin-right: 75px;
    }
}

@media (max-width: 1140px) {
    .new-wrap {
        justify-items: center;
        justify-content: center;
    }

    .new-body {
        margin-right: 35px;
    }
}

@media (max-width: 1000px) {
    .new-body {
        width: 120px;
    }

    .new-title, .new-artist {
        max-width: 110px;
    }

    .new-image {
        width: 120px;
        height: 120px;
    }
}